<template>
  <div class="hourglass">
    <input ref="uploader" type="file" @change="handleFileChange" />
    <div>{{ progress }}</div>
    <audio controls ref="instrumental"/>
    <audio controls ref="voice"/>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';
const decode = require('audio-decode');
const bufferToWav = require('audiobuffer-to-wav');

export default {
  name: 'Hourglass',
  data() {
    return {
      model: null,
      decodeContext: require('audio-context')({
        sampleRate: 22050,
        offline: true,
        length: 1
      }),
      progress: 0,
    };
  },

  methods: {
    async handleFileChange() {
      if (!this.$refs.uploader.files.length) {
        return;
      }

      const file = this.$refs.uploader.files[0];
      const audio = await decode(file, {
        context: this.decodeContext,
      });
      
      let data = null;
      if (audio.numberOfChannels >= 2) {
        // Stereo to mono
        data = new Float32Array(audio.length);
        for (let j = 0; j < audio.numberOfChannels; j += 1) {
          const channelData = audio.getChannelData(j);
          for (let i = 0; i < audio.length; i += 1) {
            data[i] += channelData[i] / audio.numberOfChannels;
          }
        }
      } else {
        data = audio.getChannelData(0);
      }

      this.makePrediction(data);
    },

    makePrediction(data) {
      const model = this.model;
      const istftHalf = this.istftHalf;
      const stftIterator = this.stftGenerator(data, { nFft: 1023, hopLength: 256 });
      let coordMap;

      const instrumentalWave = new Float32Array(data.length);
      const voiceWave = new Float32Array(data.length);
      let iterateCount = 0;

      makeBatchPrediction();

      const predictCallback = () => {
        var instrumentalBuffer = this.decodeContext.createBuffer(1, instrumentalWave.length, 22050);
        instrumentalBuffer.copyToChannel(instrumentalWave, 0);

        var instrumentalBlob = new window.Blob(
          [ new DataView(bufferToWav(instrumentalBuffer)) ],
          { type: 'audio/wav' },
        );

        var voiceBuffer = this.decodeContext.createBuffer(1, voiceWave.length, 22050);
        voiceBuffer.copyToChannel(voiceWave, 0);

        var voiceBlob = new window.Blob(
          [ new DataView(bufferToWav(voiceBuffer)) ],
          { type: 'audio/wav' },
        );

        this.$refs.instrumental.src = URL.createObjectURL(instrumentalBlob);
        this.$refs.voice.src = URL.createObjectURL(voiceBlob);
      }

      function makeBatchPrediction() {
        const frames = tf.tidy(() => {
          if (!coordMap) {
            coordMap = tf.keep(tf.tile(tf.range(0, 1, 1 / 512).reshape([512, 1, 1]), [1, 64, 1]));
          }

          const { value: stftValue, done: stftDone} = stftIterator.next();
          if (stftDone) {
            return null;
          }

          const [ stftReal, stftImag ] = stftValue;
          const stftMag = tf.sqrt(tf.add(stftReal.square(), stftImag.square()));
          const stftPhase = tf.atan2(stftImag, stftReal);

          const inputs = [stftMag.reshape([1, 512, 64, 1]), coordMap.expandDims()];
          const prediction = model.predict(inputs).squeeze([0]);

          const predictionReal = tf.mul(prediction, tf.cos(stftPhase).expandDims(2));
          const predictionImag = tf.mul(prediction, tf.sin(stftPhase).expandDims(2));

          const [instrumentalReal, voiceReal] = tf.unstack(predictionReal, 2);
          const [instrumentalImag, voiceImag] = tf.unstack(predictionImag, 2);
          /*return [
            istftHalf(stftReal, stftImag, { nFft: 1023, hopLength: 256 }),
            istftHalf(stftReal, stftImag, { nFft: 1023, hopLength: 256 }),
          ];*/
          return [
            istftHalf(instrumentalReal, instrumentalImag, { nFft: 1023, hopLength: 256 }),
            istftHalf(voiceReal, voiceImag, { nFft: 1023, hopLength: 256 }),
          ];
        });

        if (!frames) {
          predictCallback();
          return;
        }
        
        for (let i = 0; i < frames[0].length; i += 1) {
          const ind = (i + iterateCount * 64) * 256;
          const frameWave = frames[0][i].dataSync();
          for (let j = 0; j < frameWave.length && ind + j < data.length; j += 1) {
            instrumentalWave[ind + j] += frameWave[j];
          }
        }
        for (let i = 0; i < frames[1].length; i += 1) {
          const ind = (i + iterateCount * 64) * 256;
          const frameWave = frames[1][i].dataSync();
          for (let j = 0; j < frameWave.length && ind + j < data.length; j += 1) {
            voiceWave[ind + j] += frameWave[j];
          }
        }

        // Free memory
        frames[0].forEach(frame => frame.dispose());
        frames[1].forEach(frame => frame.dispose());

        iterateCount += 1;
        setTimeout(makeBatchPrediction, 0);
      }
    },

    istftHalf(stftMatrixReal, stftMatrixImag) {
      const signalFrames = tf.tidy(() => {
        const width = stftMatrixReal.shape[0];
        const stftReflectedReal = stftMatrixReal.slice(1, width - 2).reverse(0);
        const stftReflectedImag = stftMatrixImag.slice(1, width - 2).reverse(0).neg();
        const specReal = stftMatrixReal.concat(stftReflectedReal);
        const specImag = stftMatrixImag.concat(stftReflectedImag);

        const ifftFramesReal = tf.unstack(specReal, 1);
        const ifftFramesImag = tf.unstack(specImag, 1);
        const signalFrames = [];
        for (let i = 0; i < ifftFramesReal.length; i += 1) {
          const ifftFrame = tf.complex(ifftFramesReal[i], ifftFramesImag[i]);
          signalFrames.push(tf.real(tf.ifft(ifftFrame)).squeeze());
        }
        return signalFrames;
      });

      return signalFrames;
    },

    *stftGenerator(y, params) {
      const nFft = params.nFft || 2048;
      const winLength = params.winLength || nFft;
      const hopLength = params.hopLength || Math.floor(winLength / 4);
      const sampleSize = params.sampleSize || 64;

      const windowDomain = tf.range(0, winLength);
      const fftWindow = tf.keep(tf.mul(
        0.5,
        tf.sub(1, tf.cos(tf.mul(2 * Math.PI / winLength, windowDomain))),
      ));

      const stftRealBuffer = [];
      const stftImagBuffer = [];

      const height = 1 + Math.floor(nFft / 2);

      for (let i = 0; ; i++) {
        const ind = i * hopLength;
        this.progress = ind / y.length;
        const buffer = y.slice(ind, ind + nFft);
        if (buffer.length < nFft) {
          break;
        }

        const [colRealSliced, colImagSliced] = tf.tidy(() => {
          const winBuffer = tf.mul(tf.tensor1d(buffer), fftWindow);
          const col = tf.fft(winBuffer.asType('complex64'));
          const colReal = tf.real(col).squeeze();
          const colImag = tf.imag(col).squeeze();
          return [colReal.slice(0, height), colImag.slice(0, height)];
        });
        stftRealBuffer.push(colRealSliced);
        stftImagBuffer.push(colImagSliced);

        if (stftRealBuffer.length >= sampleSize) {
          const stftResult = [tf.stack(stftRealBuffer, 1), tf.stack(stftImagBuffer, 1)];
          stftRealBuffer.forEach(tensor => tensor.dispose());
          stftImagBuffer.forEach(tensor => tensor.dispose());
          stftRealBuffer.length = 0;
          stftImagBuffer.length = 0;
          yield stftResult;
        }
      }

      fftWindow.dispose();
    },
  },

  async created() {
    this.model = await tf.loadModel('models/hourglass/model.json');
    window.model = this.model;
  }
}
</script>

